<template>
  <div>
        <el-form ref="addForm" :rules="rules" :model="dataForm" label-width="150px">
            <el-row>
                <el-col :span="22">
                    <el-form-item label="分类库名称" prop="mingceName">
                        <el-input type="text" v-model="dataForm.mingceName" maxlength='15' placeholder="请输入名册名称"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="22">
                    <el-form-item label="PAD中简称" prop="padName">
                        <el-input type="text" v-model="dataForm.padName" maxlength='10' placeholder="请输入PAD中简称"></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <!-- <el-row>
                <el-col :span="22">
                    <el-form-item label="名册类别" prop="mingceType">
                        <el-radio-group v-model="dataForm.mingceType">
                            <el-radio :label="1">机构名册</el-radio>
                            <el-radio :label="2">分类名册</el-radio>
                        </el-radio-group>
                    </el-form-item>
                </el-col>
            </el-row> -->
            <!-- <el-row>
                <el-col :span="22">
                    <el-form-item label="名册说明" prop="mingceDesc">
                        <el-input type="textarea" :rows="5" v-model="dataForm.mingceDesc"></el-input>
                    </el-form-item>
                </el-col>
            </el-row> -->
            <div class="dialog-footer">
                <el-button type="primary" @click="saveArea()">保存</el-button>
                <el-button type="danger" @click="cancle()">取消</el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default{
        props: [
                'checkedData',
                'isEdit'//true：添加，false 编辑
        ],
        data(){
            return{
                //表单验证
                rules: {
                    mingceName: [{
                        required: true,
                        message: '请输入名册名称',
                        trigger: 'change'
                    }],
                },
                dataForm:{
                    mingceName:'',//名册名称
                    padName:'',//PAD中简称
                    mingceType:3,//名册类别
                    mingceId:''
                },
               
            }
        },
        created(){ 
            this.setFormData()
        },
        methods:{
            setFormData(){
                if(this.isEdit === false){
                    this.dataForm.mingceName = this.checkedData.name
                    this.dataForm.padName = this.checkedData.padName
                    this.dataForm.mingceType = Number(this.checkedData.type)
                    this.dataForm.mingceDesc = this.checkedData.mingceDesc
                    this.dataForm.mingceId  = this.checkedData.id 
                }
            },

            //保存
            saveArea(){
                this.$refs['addForm'].validate((valid) => {
                    if(valid) {
                        this.$http({
                            url: this.$http.adornUrl(this.API.saveOrUpdateMingce),
                            method: 'post',
                            data: this.$http.adornData(
                                this.dataForm
                            )
                        }).then((res) =>{
                            var resData = res.data;
                            if(resData.code == 0) {
                                this.$message({
                                    message: '成功',
                                    type: 'success'
                                });
                                this.$emit("addback",true);
                            } else {
                                this.$message.error(resData.msg);
                            }
                        })
                    } 
                })
            },
            //取消
            cancle() {
                this.$emit("addback",false);
            } 
        }
    }
</script>

<style>
</style>



