<template>
    <div class="box">
         <el-main>
            <div class="table-header-btn">
                <div class="table-header-btn-l"></div>
                <div class="table-header-btn-r">
                    <el-button type="primary" size="small" @click="addListClick()">添加</el-button>
                    <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
                    <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
                    <el-button type="primary" size="small"  @click="saveMetaSort" >保存排序</el-button>
                </div>   
            </div>
            <el-table 
                ref="multipleTable" 
                :data="tableData" 
                @selection-change="selectionChangeHandle"
            >
                <el-table-column type="selection" width="50" align="center"></el-table-column>
                <el-table-column width="50" fixed type="index" :index="indexMethod" label="序号" align="center"></el-table-column>
                <el-table-column prop="conditionDesc" label="描述" ></el-table-column>
                <el-table-column label="是否启用">
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.status == 1" size="small">是</el-tag>
                        <el-tag v-else size="small" type="danger">否</el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="200">
                    <template slot-scope="scope">
                        <el-button type="primary" size="mini" plain @click="edikListClick(scope.$index,scope.row)">编辑</el-button>
                        <el-button type="danger" size="mini" plain @click="deleteListClick(scope.$index,scope.row)" >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-main>
        
         <!-- 干部类别设置、新增-->
        <el-dialog 
            :title="title" 
            :visible.sync="cadreContDialogVisable" 
            width="80%" 
            v-if="cadreContDialogVisable" 
            :close-on-click-modal ="false"  
        >
            <cadre-cont-dialog :checkedData="checkedData" :isEdit="isEdit" ref="addContDialog" @addback="addback"></cadre-cont-dialog>
        </el-dialog>
        
    </div>
</template>
<script>
    import {swapItems} from '@/utils';
    import cadreContDialog from './cadre-cont-dialog.vue'
    export default{
        components:{
            cadreContDialog
        },
        data(){
            return{
                tableData:[],
                cadreContDialogVisable:false,
                treeData:[],
            }
        },
        created(){
            
        },
        methods:{
            getDataList(data){
                this.treeData = data;
                this.$http({
                    url: this.$http.adornUrl(this.API.queryPersonMingceConditionAll),
                    method: 'post',
                    data: this.$http.adornData({
                        mingceId:data.id
                    })
                }).then(({data}) => {
                    if(data && data.code===0){
                        this.tableData = data.data;
                    }else{
                        this.$message.error(data.msg);
                    }
                });
            },
            //右侧列表添加
            addListClick(){
                this.checkedData = this.treeData;
                this.title = '干部类别添加';
                this.cadreContDialogVisable = true;
                this.isEdit = true;
            },
            //右侧列表编辑
            edikListClick(index,row){
                this.checkedData = this.treeData;
                this.title = '干部类别修改';
                this.cadreContDialogVisable = true;
                this.isEdit = false;
                this.$nextTick(() => {
                    this.$refs.addContDialog.getDataList(row);
                }) 
            },
            //右侧列表删除
            deleteListClick(index,row){
                this.$confirm('确定删除选中记录?', '操作提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$http({
                        url: this.$http.adornUrl(this.API.deletePersonMingceCondition),
                        method: 'post',
                        data: this.$http.adornData({
                            conditionId:row.id
                        })
                    }).then(({data}) =>{
                        if(data.code === 0) {
                            this.getDataList();
                            this.$message.success('删除成功');
                        } else {
                            this.$message.error(data.msg);
                        }
                    })
                }).catch(() => {
            
                });
            },   
            selectionChangeHandle(val) {//多选
                this.tableDataSelections = val;
            },
            moveDirection(direction){ //移动
                if(this.tableDataSelections.length === 0) {
                    this.$confirm('请选择一条数据！', '提示', {
                        confirmButtonText: '确定',
                        showCancelButton: false,
                        type: 'warning',
                        center: true
                    });
                    return;
                };
                var that=this,index;
                that.tableDataSelections.sort((prev,next)=>{
                    return that.tableData.indexOf(prev)-that.tableData.indexOf(next)
                })
                if(direction=='down') that.tableDataSelections.reverse()
                that.tableDataSelections.forEach(item=>{
                    index=that.tableData.indexOf(item); //得到右侧选中值的标识坐标
                    if(direction=='up') that.moveUp(index);
                    else if(direction=='down') that.moveDown(index)
                })
            },
            moveUp(index){//上移
                if(index==0) return;
                this.tableData=swapItems(this.tableData,index,index-1,'up');
            },
            moveDown(index){ //下移
                if(index==this.tableData.length-1) return;
                this.tableData=swapItems(this.tableData,index,index+1,'down');
            },
            saveMetaSort(){//保存排序
                var sortArr=[];
                if(this.tableData.length<0) return;
                this.tableData.forEach((item,index)=>{
                    sortArr.push({
                        id:item.conditionId,
                        orderno:index+1
                    })
                })
                this.$http({
                    url: this.$http.adornUrl(this.API.personMingceConditionSort),
                    method: 'post',
                    data: this.$http.adornData({
                        data:JSON.stringify(sortArr)
                    })
                }).then(({data}) => {
                    if(data && data.code===0){
                        this.getDataList(this.treeData)
                        this.$message.success('操作成功');
                    }else{
                        this.$message.error(data.msg);
                    }
                });
            },
            indexMethod(index) {
                return index + 1;
            },
            // 添加或修改组件回调方法--子组件回调数据的方法
            // @param {Boolean} isRefresh是否需要刷新数据 true=刷新；false=不刷新
            addback(isRefresh){
                this.cadreContDialogVisable = false;
                if(isRefresh){
                    this.getDataList(this.treeData)
                }
            },
        }
    }
</script>
<style lang="scss" scoped>
    .el-main {
        padding:0;
    }
</style>


