<template>
  <div>
    <div class="float-right">
      <el-checkbox v-model="checked">关联子节点</el-checkbox>
      <el-button type="primary" size="small" @click="getCurrentKey">保存</el-button>
    </div>
    <div class="clearfix"></div>
    <el-scrollbar style='height: 400px;'>
      <org-tree ref="tree"
        :treeData="treeData"
        :checkbox='treeCheckBox'
        :checkStrictly='!checked'
        :defaultChecked='defaultChecked'
      ></org-tree>
    </el-scrollbar>
  </div>
</template>

<script>
    import orgTree from "@/components/baseComponents/orgTree.vue";
    export default{
        components: {
            orgTree
        },
        data(){
            return{
                mingceId:'',
                checked:true,
                treeData:[],
                treeCheckBox:true,//tree复选框是否有
                defaultChecked:[],
            }
        },
        methods:{
            getData(data){
                this.mingceId = data.id;
                this.$http({
                    url: this.$http.adornUrl(this.API.getMingceB01TreeB01),
                    method: 'post',
                    data: this.$http.adornData({
                        mingceId:data.id, 
                    })
                }).then(({data}) => {
                    if(data && data.code===0){
                        this.treeData=data.data;
                        this.defaultChecked=this.getIsCheckCurrent(data.data);
                    }else{
                        this.$message.error(data.msg);
                        this.treeData=[];
                        this.defaultChecked=[];
                    }
                });
            },
            getCurrentKey(){//保存选中节点
                // console.log(this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys()).join(','))
                this.$http({
                    url: this.$http.adornUrl(this.API.savePersonMingceB01),
                    method: 'post',
                    data: this.$http.adornData({
                        mingceId:this.mingceId, 
                        b0100:this.$refs.tree.getCheckedKeys().join(','),
                    })
                }).then(({data}) => {
                    if(data && data.code===0){
                        this.$message.success('操作成功');
                    }else{
                        this.$message.error(data.msg);
                    }
                });
            },
            getIsCheckCurrent(arr){//多层树结构中取出是否选中
                const setCheckedKeys=[];
                if(!arr) return;
                const getChild=(arr)=>{
                    arr.forEach(item=>{
                        if(item.isCheck) setCheckedKeys.push(item.id);
                        if(item['children'] && item['children'].length>0){
                            getChild(item.children)
                        }
                    })
                    return setCheckedKeys;
                }
                return getChild(arr)
            },
        }
    }
</script>

<style lang="scss" scoped>
  .float-right{
    padding: 10px 20px 10px 0;
    .el-checkbox{padding-right: 20px;}
  }
</style>



