<template>
    <div class="metaData">
        <el-container style="height: 100%;">
            <el-aside width="274px" >
                <div class="table-header-btn">
                    <div class="table-header-btn-l">
                        <el-button type="primary" size="small" @click="addClick()">添加</el-button>
                        <el-button type="primary" size="small" @click="edikClick()">编辑</el-button>
                        <el-button type="danger" size="small" @click="deleteClick()" >删除</el-button>
                        <el-button type="primary" size="small" @click="sortClick()" >排序</el-button>
                    </div>
                </div>
                <el-scrollbar :style="Height">
                    <metadataTree ref="moduleTree" :treeData="treeData" @node-click="orgTreeClick" style="margin-top:10px;"></metadataTree>
                </el-scrollbar>
            </el-aside>
            <el-main v-show="isBtn">
                <div class="table-header-btn">
                    <div class="table-header-btn-l"></div>
                    <div class="table-header-btn-r">

                    </div>
                </div>
                <el-tabs v-model="activeName" @tab-click="handleClick"  type="card">
					<el-tab-pane label="机构设置" name="tabFirst" :key="'tabFirst'">
                        <organization-set ref="organizationSet"></organization-set>
					</el-tab-pane>
					<el-tab-pane label="干部类别设置" name="tabTwo" :key="'tabTwo'">
						<cadre-category-set ref="cadreCategorySet"></cadre-category-set>
					</el-tab-pane>
                    <el-tab-pane label="排序设置" name="tabLast" :key="'tabLast'">
                        <sort-set  ref="sortSet"  @addback="addback"></sort-set>
					</el-tab-pane>
                    <el-tab-pane label="名册分享" name="tabRegister" :key="'tabRegister'">
                        <register-share ref="registerShare"></register-share>
					</el-tab-pane>
				</el-tabs>
            </el-main>
        </el-container>

        <el-dialog
            :title="title"
             width="50%"
            :visible.sync="addOneVisable"
            v-if="addOneVisable"
            :close-on-click-modal ="false"
            :modal-append-to-body='false'
            append-to-body
        >
            <add-one-name :checkedData="checkedData" :isEdit="isEdit" @addback="addback"></add-one-name>
        </el-dialog>

        <el-dialog
            title="分类库排序"
            :visible.sync="sortNameVisable"
            width="50%"
            v-if="sortNameVisable"
            @close='closeDialog'
        >
            <sort-name :checkedData="checkedData"  @addback="addback"></sort-name>
        </el-dialog>
    </div>
</template>
<script>
import metadataTree from "@/components/baseComponents/orgTree.vue";
import addOneName from "./add-one-name.vue";
import sortName from "./sort-name.vue";
import organizationSet from './organization-set.vue';
import cadreCategorySet from './cadre-category-set.vue'
import sortSet from './sort-set.vue';
import registerShare from './register-share.vue';
export default {
    name: "register-management-index",
    components: {
        metadataTree,
        addOneName,
        sortName,
        organizationSet,
        cadreCategorySet,
        sortSet,
        registerShare
    },
    data() {
        return {
            Height: {
                height: document.documentElement.clientHeight - 135 + "px"
            },
            tableData:[],
            treeData:[],
            isEdit: '', //true 添加，false：修改
            addOneVisable:false,
            sortNameVisable:false,
            currentRow:'',//选中的树数据
            isBtn:false,
            activeName: "tabFirst",
        }
    },
    created(){
       this.getDataTree();
    },
    methods: {
        //获取树
        getDataTree(){
            this.$http({
                url: this.$http.adornUrl(this.API.getMingceTreeListMingce),
                method: 'post',
                data: this.$http.adornData({
                    mingceType:3,
                })
            }).then(({data}) => {
                if(data && data.code===0){
                    this.treeData=data.data;
                    // this.orgTreeClick(this.treeData[0])
                }else{
                    this.$message.error(data.msg);
                    this.treeData=[];
                }
            });
        },
        //树点击事件
        orgTreeClick(data) {
            this.currentRow = data;
            this.isParent = data.isParent;
            if(this.isParent === false){
                this.isBtn = true;
            }else if(this.isParent === true){
               this.isBtn = true;
            }
            this.isActiveInit(this.activeName,data)
        },
        handleClick(tab) {
            this.isActiveInit(tab.name,this.currentRow)
        },
        isActiveInit(name,data){
            this.$nextTick(() => {
                if( name == 'tabFirst') {
                    this.$refs.organizationSet.getData(data);
                } else if( name == 'tabTwo'){
                    this.$refs.cadreCategorySet.getDataList(data);
                }else if( name == 'tabLast'){
                    this.$refs.sortSet.getDataList(data)
                }else if(name == 'tabRegister'){
                    this.$refs.registerShare.getData(data)
                }
            })
        },
        //左侧树、分类库添加
        addClick(){
            this.checkedData = this.currentRow;
            this.addOneVisable = true;
            this.title ="分类库添加";
            this.isEdit =true;
        },

        //左侧树、分类库编辑
        edikClick(){
            if(this.currentRow === null || this.currentRow === "") {
                this.$confirm('请选择一条数据！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            }
            this.checkedData = this.currentRow;
            this.addOneVisable = true;
            this.title ="分类库编辑";
            this.isEdit = false;
        },

        //左侧树、分类库排序
        sortClick(){
            this.checkedData = this.currentRow;
            this.sortNameVisable = true;
        },

        //左侧树、删除名册目录
        deleteClick(){
            if(this.currentRow === null || this.currentRow === "") {
                this.$confirm('请选择一条数据！', '提示', {
                    confirmButtonText: '确定',
                    showCancelButton: false,
                    type: 'warning',
                    center: true
                });
                return;
            }
            this.$confirm('确定删除选中记录?', '操作提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$http({
                    url: this.$http.adornUrl(this.API.deleteMingce),
                    method: 'post',
                    data: this.$http.adornData({
                        mingceId:this.currentRow.id
                    })
                }).then(({data}) =>{
                    if(data.code === 0) {
                        this.getDataTree();
                        this.$message.success('删除成功');
                    } else {
                        this.$message.error(data.msg);
                    }
                })
            }).catch(() => {

            });
        },
        //树排序关闭回调
        closeDialog() {
           this.getDataTree()
        },
        // 添加或修改组件回调方法--子组件回调数据的方法
	    // @param {Boolean} isRefresh是否需要刷新数据 true=刷新；false=不刷新
        addback(isRefresh){
            this.addOneVisable = false;
            if(isRefresh){
                this.getDataTree();
            }
        },
    },
}
</script>
<style lang="scss" scoped>
    .el-aside {
        border-right: 1px solid #dcdfe6;
        padding-top: 10px;
    }
    .el-main {
        padding:0;
    }
    .el-form-item {
        margin-bottom: 0px;
    }
</style>



